.sdk-container {
    display: flex;
    flex-direction: row;
    margin-top: 16px;

    .sdk-config {
        width: calc(50% - 21px);
        .basic-config{
            margin-top: 10px;
            display: flex;
            flex-direction: column;
        }
    }

    .code-copy {
        width: calc(50% - 21px);

        .copy-btn {
            position: absolute;
            top: 16px;
            right: 25px;
            cursor: pointer;
        }

        .code-display {
            max-height: calc(100vh - 420px);
            background: #F6F7FB;
            color: #1D2129;
            margin-top: 16px;
            padding: 12px;
            border-radius: 4px;
            white-space: pre-wrap;
            overflow-y: auto;
        }
    }
}